<template>
  <g>
    <path :d="pathString" stroke-dasharray="15,10" class="path"/>
  </g>
</template>

<script>
  export default {
    components: {},
    props: {
      route:{
        type: Array
      }
    },
    data () {
      return {

      }
    },
    mounted(){

    },
    watch:{
    },
    computed:{
      pathString() {
        let path_str = 'M '+this.route[0].x*100+','+((-this.route[0].y)*100);
        path_str += ' L'+this.route[1].x*100+','+((-this.route[1].y)*100);
        for(let i=2;i<this.route.length;i++) {
          path_str += ' L'+this.route[i].x*100+','+((-this.route[i].y)*100);
        }
        console.log(path_str);
        return path_str;
      },
    },
    methods: {
    }
  }
</script>

<style scoped>
.path{
  fill:none;stroke:#ee0000;stroke-width:6px;
}
</style>